html, body {
    margin:0px;
    padding:0px;
    height:100%;
    width:100%;
    font-family:"Merriweather";
    line-height: normal;
    color:$gray;
    height: 100%;
    min-height: 100%;


}

body {
    overflow:visible;
    overflow-y: scroll; /* has to be scroll, not auto */
    -webkit-overflow-scrolling: touch;
}

body.menu-open {
    overflow-y:hidden;


}

#site-wrapper {
    display:block;
    width:100%;
    height:100%;
    clear:both;
    *zoom:1;
    overflow: visible;
    position:relative;
    -webkit-transition:-webkit-transform 0.5s;
    transition:-webkit-transform 0.5s;
    transition:transform 0.5s;
    transition:transform 0.5s, -webkit-transform 0.5s;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;

}

#site-wrapper:before {
    content:'';
    display:table;


}

#site-wrapper:after {
    content:'';
    display:table;
    clear:both;


}

#site-wrapper:before, #site-wrapper:after {
    content:" ";
    clear:both;
    display:block;


}

.section.container {
    position:relative;
}

.container {
    display:-webkit-box;
    display:-webkit-flex;
    display:-ms-flexbox;
    display:flex;
    -webkit-flex-flow:row wrap;
        -ms-flex-flow:row wrap;
            flex-flow:row wrap;
    max-width:100%;
    margin-left:auto;
    margin-right:auto;
}

@media (min-width: $(tablet)) {

    .container {
        display:-webkit-box;
        display:-webkit-flex;
        display:-ms-flexbox;
        display:flex;
        -webkit-flex-flow:row wrap;
            -ms-flex-flow:row wrap;
                flex-flow:row wrap;
        max-width:740px;
        margin-left:auto;
        margin-right:auto;


    }


}

@media (min-width: $(desktop)) {

    .container {
        display:-webkit-box;
        display:-webkit-flex;
        display:-ms-flexbox;
        display:flex;
        -webkit-flex-flow:row wrap;
            -ms-flex-flow:row wrap;
                flex-flow:row wrap;
        max-width:960px;
        margin-left:auto;
        margin-right:auto;


    }


}

@media (min-width: $desktop-large) {

    .container {
        display:-webkit-box;
        display:-webkit-flex;
        display:-ms-flexbox;
        display:flex;
        -webkit-flex-flow:row wrap;
            -ms-flex-flow:row wrap;
                flex-flow:row wrap;
        max-width:1160px;
        margin-left:auto;
        margin-right:auto;


    }


}

.icon {
    display:inline-block;
}

